:root {
  --primary-font: "Roboto", sans-serif;
  --primary-text-color: #2c2c2c;
  --secondary-text-color: #505050;
  --form-text-color: #c4c4c4;
  --header-footer-background-color: #fefefe;
  --primary-background-color: #f5f5f5;
  --yellow-main: #ffe550;
  --btn-green: #bbf330;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

.list {
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.btn {
  cursor: pointer;
  border: none;
  padding: 0;
}

body {
  font-family: var(--primary-font);
  color: var(--primary-text-color);
  background-color: var(--header-footer-background-color);
  font-style: normal;
  font-weight: 400;
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 734px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1278px;
  }
}

.section {
  padding: 60px 0;
}

@media screen and (min-width: 768px) {
  .section {
    padding: 100px 0;
  }
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-dur-and-func), visibility var(--transition-dur-and-func);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu {
  width: 130px;
  height: 255px;
  background-color: var(--header-footer-background-color);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  border-radius: 30px 0 30px 30px;
  font-size: 16px;
  line-height: 19px;
  position: absolute;
  top: 50px;
  right: 20px;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.mobile-menu__close-btn {
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 4px;
  right: 4px;
}

.mobile-menu__close-icon {
  display: block;
}

.mobile-menu__list {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.mobile-menu__item {
  padding: 16px;
}

.mobile-menu__item:first-child {
  border-radius: 30px 0 0;
}

.mobile-menu__item:last-child {
  border-radius: 0 0 30px 30px;
}

.mobile-menu__item:hover, .mobile-menu__item:focus, .mobile-menu__item:active {
  background-color: var(--btn-green);
  cursor: pointer;
}

.mobile-menu__item:focus-within {
  background-color: var(--btn-green);
}

.mobile-menu__link:focus {
  outline: none;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

@media screen and (min-width: 768px) {
  .mobile-menu__close-btn, .mobile-menu, .mobile-menu-btn {
    display: none;
  }
}

.modal {
  width: 320px;
  height: 449px;
  background-color: var(--yellow-main);
  border-radius: 30px;
  padding: 32px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal__close-btn {
  width: 32px;
  height: 32px;
  transition: box-shadow var(--transition-dur-and-func);
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal__close-btn:hover, .modal__close-btn:focus {
  box-shadow: 0 4px 4px rgba(0, 0, 0, .3);
}

.modal__close-btn-icon {
  fill: #2c2c2c;
  transition: fill var(--transition-dur-and-func);
}

.modal__close-btn:hover .modal__close-btn-icon, .modal__close-btn:focus .modal__close-btn-icon {
  fill: #000;
}

@media screen and (min-width: 768px) {
